import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation, useQuery, useQueryClient } from 'react-query'

import { CLUB_FORMS_MESSAGE, COMMON_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/Club'
import type { Form } from 'src/models/Form'
import type { Recruit } from 'src/models/Recruit'
import type { DateProps } from 'src/models/date'

interface GetFormParameters {
  clubId: ClubId
  date?: DateProps
  token: string
}

export type GetFormResponseData = Form

const getFormsAPICall =
  ({ clubId, date, token }: GetFormParameters) =>
  async () => {
    const response = await axios.get<GetFormResponseData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/form`,
      {
        params: date ?? null,
        headers: { Authorization: token }
      }
    )
    return response.data
  }

export const useGetForms = (clubId: ClubId, date?: DateProps) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<GetFormResponseData>(['forms', date], getFormsAPICall({ clubId, date: date, token }), {
    onError: error => {
      if (!(error instanceof AxiosError)) {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    },
    retry: 0
  })
}

export type PutFormRequesData = Form & Pick<Recruit, 'startAt' | 'endAt'>

export type PutFormParams = {
  clubId: ClubId
  date: DateProps
  recruitId: number
} & PutFormRequesData

const putFormAPICall = async ({ clubId, questions, startAt, endAt, recruitId }: PutFormParams, token: string) => {
  await axios.put<null, AxiosResponse<null>, PutFormRequesData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}/form`,
    { questions, startAt, endAt },
    {
      headers: { Authorization: token }
    }
  )
}

export const useEditForm = (): UseMutationResult<void, unknown, PutFormParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PutFormParams) => putFormAPICall(values, token)

  return useMutation('putForm', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(CLUB_FORMS_MESSAGE.EDIT_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('forms')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(CLUB_FORMS_MESSAGE.EDIT_FAIL, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
